const SECTIONS = {
  Torah: "",
  Prophets: "",
  Writings: "",
};

const MISC = {
  of: "",
  "more than": " ",
};
const HEBREW = {
  Section: "",
  Book: "",
  Chapter: "",
  Verse: "",
  Text: "",
  Search: "",
  All: "",
  ...SECTIONS,
  ...BOOK_NAMES,
  ...MISC,
};

const getText = (text, lang) => {
  if (lang === "en") {
    return text;
  }
  return HEBREW[text] || text;
};

export { getText };
